// Seo

.seo {
	padding: 80px 0 0;
	font-size: 15px;
	line-height: 20px;
	@include rmin($tablet) {
		padding: 115px 0 0;
		font-size: 18px;
		line-height: 25px; }
	@include rmin($desktop); }



.seo__heading {
	font-size: 20px;
	line-height: 25px;
	font-weight: 900;
	margin: 0 0 20px;
	text-transform: uppercase;
	@include rmin($tablet) {
		font-size: 25px;
		line-height: 1;
		margin: 0 0 30px; } }

.seo__collapse {
	position: relative;
	max-height: 240px;
	overflow: hidden;
	transition: 0.4s;
	margin: 0 0 20px;
	&:after {
		content: '';
		display: block;
		position: absolute;
		left: 0;
		bottom: 0;
		height: 78px;
		width: 100%;
		// background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.9) 3%, #ffffff)
		background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
		transition: inherit; }
	p {
		margin: 0 0 20px;
		&:last-child {
			margin: 0; } }
	&[aria-expanded="true"]:after {
		height: 0; }
	@include rmin($desktop) {
		max-height: 475px; } }

.seo__image {
	display: none;
	margin: 0;
	img {
		display: block;
		width: 100%; }
	@include rmin($desktop) {
		display: block; } }
