.grid-buttons {
	position: fixed;
	left: 0;
	bottom: 0;
	z-index: 10000; }

.js-desktop, .js-tablet, .js-mobile {
    display: block;
    padding: 0 10px;
    font-weight: 900;
    line-height: 40px;
    height: 40px;
    background: #000;
    z-index: 100000;
    font-size: 12px;
    color: #fff;
    width: 100px;
    cursor: pointer;
    transition: 0.25s;
    margin: 0 0 10px;
    display: none;
    &:hover {
        background: #C43939; } }

.js-mobile {
    display: block;
    @include rmin($tablet) {
        display: none; } }

.js-tablet {
    @include rmin($tablet) {
        display: block; }
    @include rmin($wide) {
        display: none; } }

.js-desktop {
    @include rmin($wide) {
        display: block; } }


