#grid-desktop {
    display: block;
    position: absolute;
    top: 0;
    left: 50%;
    width: 1440px;
    height: 7230px;
    // height: 3065px
    margin: 0 0 0 -720px;
    z-index: 10000;
    background: url(../img/grid-company-desktop.png) 0 0 no-repeat;
    opacity: 0.5;
    visibility: hidden;
    display: none;
    &.is-active {
        visibility: visible;
        display: block; } }

#grid-tablet {
    display: block;
    position: absolute;
    top: 0;
    left: 50%;
    width: 768px;
    height: 6930px;
    // height: 3065px
    margin: 0 0 0 -384px;
    z-index: 10000;
    background: url(../img/grid-company-tablet.png) 0 0 no-repeat;
    opacity: 0.5;
    visibility: hidden;
    display: none;
    &.is-active {
        visibility: visible;
        display: block; } }

#grid-mobile {
    display: block;
    position: absolute;
    top: 0;
    left: 50%;
    width: 376px;
    height: 5865px;
    // height: 3065px
    margin: 0 0 0 -188px;
    z-index: 10000;
    background: url(../img/grid-company-phone.png) 0 0 no-repeat;
    opacity: 0.5;
    visibility: hidden;
    display: none;
    &.is-active {
        visibility: visible;
        display: block; } }


.row-0 {
    margin: 0;
    & > [class^="col"] {
        padding: 0; } }


