// Contacts

.contacts {
	padding: 0 0 10px; }

.contacts__container {
	position: relative;
	z-index: 1;
	top: 5px;
	background: #f2f1ed;
	margin: 0 -30px;
	padding: 0;
	// .row
	// 	margin: 0
	// 	& > [class^="col"]
	// 		padding: 0
	&:after {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		left: 50%;
		bottom: 0;
		width: 100vw;
		margin: 0 0 0 -50vw;
		background: #f2f1ed;
		z-index: -1; }
	@include rmin($tablet) {
		margin: 0 -20px; }
	@include rmin($xl) {
		top: -28px;
		margin: 0 -20px;
		padding: 0 20px 0 9.2%; } }


.contacts__group {
	.row {
		margin: 0;
		& > [class^="col"] {
			padding: 0; } }
	@include rmin($xl) {
		// width: 370px
 } }		// padding: 0 0 0 10px

.contacts__address {
	position: relative;
	z-index: 10;
	display: block;
	margin: 0;
	font-size: 15px;
	line-height: 20px;
	font-style: normal;
	padding: 30px 30px 22px;
	min-height: 100%;

	&--dark {
		background: #252525;
		color: #fff;
		border: none;
		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: 50%;
			bottom: 0;
			width: 120vw;
			margin: 0 0 0 -60vw;
			z-index: -1;
			background: #252525;
			@include rmin($tablet) {
				margin: 0;
				left: auto;
				right: 0; }
			@include rmin($xl) {
				right: -40px; } } }


	strong {
		display: block;
		font-size: 20px;
		line-height: 1;
		font-weight: 900;
		margin: 0 0 22px;
		text-transform: uppercase; }
	p {
		margin: 0 0 20px;
		&:last-child {
			margin: 0; } }
	@include rmin($tablet) {
		font-size: 18px;
		line-height: 25px;
		padding: 38px 20px 38px;
		strong {
			font-size: 25px;
			line-height: 30px; }
		p {
			margin: 0 0 26px; } }
	@include rmin($xl) {
		border-bottom: 3px solid #252525;
		padding-left: 0;
		&:last-of-type {
			border: none; } } }


.contacts__map {
	display: block;
	height: 549px;
	background: #ccc;
	margin: 0 -30px;
	@include rmin($tablet) {
		height: 590px;
		margin: 0 -20px; }
	@include rmin($xl) {
		height: 100%;
		margin: 0;
		min-width: 66.666vw;
		// // height: 840px
		// height: 100%
