// buttons
button {
    -webkit-apperance: none;
    border: 0;
    box-shadow: none;
    &:focus {
        outline: none; } }

.btn {
    display: inline-block;
    vertical-align: top;
    position: relative;
    text-align: center;
    font: 900 25px/30px $fontBase;
    padding: 15px 30px;
    transition: 0.25s;
    border: none;
    cursor: pointer;
    &:focus, &:active {
        outline: none; }

    &--block {
        display: block;
        width: 100%; }
    @include rmin($tablet); }




.btn--link {
    background: none;
    font-size: 15px;
    line-height: 1;
    padding: 0 0 8px;
    border-bottom: 3px solid #000;
    &:hover {
        color: #c43939; } }


.btn--collapse {
    font-size: 15px;
    line-height: 1;
    padding: 0 0 10px;
    border-bottom: 3px solid #000;
    @include rmin($tablet) {
        font-size: 20px; }
    @include rmin($desktop) {
        font-size: 25px; } }

.btn--small {
    padding: 8px 10px 7px;
    font-size: 15px;
    line-height: 1; }

.btn--large {
    // font-size: 25px
 }    // line-height: 30px


.btn--grey {
    background: #999999;
    color: #fff;
    &:hover {
        background: #c43939;
        color: #fff; } }

.btn--red {
    background: #c43939;
    color: #fff;
    &:hover {
        box-shadow: 0 20px 60px 0 rgba(0, 0, 0, 0.25); }
    &:active {
        transition: none;
        box-shadow: 0 10px 30px 0 rgba(0, 0, 0, 0.25); } }

.btn--load {
    font-size: 15px;
    line-height: 1;
    color: #252525;
    padding: 8px 0 10px;
    &:after, &:before {
        content: '';
        display: block;
        position: absolute;
        bottom: -1px;
        left: 0;
        width: 100%;
        height: 3px;
        transition: 0.25s; }
    &:after {
        background: #252525;
        z-index: 1; }
    &:before {
        background: #c43939;
        z-index: 2;
        width: 0; }

    &:hover {
        color: #c43939; }
    @include rmin($tablet) {
        font-size: 20px; }
    @include rmin($wide) {
        font-size: 25px; } }

.btn--chat {
    border: none;
    position: fixed;
    bottom: 0;
    right: 10px;
    z-index: 200;
    text-transform: lowercase;
    font-size: 15px;
    line-height: 20px;
    padding: 5px 20px;
    width: 160px;
    background: #999999;
    color: #fff;
    &:hover {
        background: #c43939; }

    @include rmin($tablet) {
        right: 20px; }
    @include rmin($desktop) {
        padding: 3px 20px 7px;
        bottom: 125px;
        right: -65px;
        transform: rotate(90deg);
        transform-origin: 50% 50%; } }



.btn--filter, .btn--categories {
    display: block;
    width: 40px;
    height: 40px;
    background: #999999;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    backface-visibility: hidden;
    .ico {
        transition: 0.25s; }
    .ico-close {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%) scale(0);
        z-index: 10;
        font-size: 16px;
        opacity: 0; }
    &:hover {
        background: #c43939; }
    @include rmin($tablet) {
        width: 60px;
        height: 60px;
        .ico-close {
            font-size: 24px; } } }


.btn--categories {
    position: absolute;
    top: -115px;
    right: -20px;
    z-index: 110;
    @include rmin($tablet) {
        top: -165px;
        right: 0; }
    @include rmin($desktop) {
        right: auto;
        top: 105px;
        left: 50%;
        margin: 0 0 0 -50vw; }
